 .mask {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.mask::before {
    content: "";
    background-image: url('../assets/2.webp');
    background-size: cover;
    background-position: center top;
    background-attachment: fixed;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.7;
    z-index: 1;
}

.hero {
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 2;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
    z-index: 3;
}

.hero .content h1 {
    font-size: 1.9rem;
    padding: 0.6rem 0 1.5rem;
    
}
.typed-web-developer, .typed-ai-explorer, .typed-web-designer {
    color: #f3f05a; /* Example color for Web-Developer */
    
  }
  
  

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
    text-align: center;
}

.content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px) {
    .mask::before{
        background-size: cover; 
        background-position: center center; 
        background-attachment: scroll;
    }
    .hero .content h1 {
        font-size: 1.8rem;
    }

    .hero .content p {
        font-size: 1.4rem;
    }
}
